/* Styles for HeroHome block (editor + frontend) */
.wp-block-eara-hero-home {
  width: 100%;
}

.wp-block-eara-hero-home .eara-hero-home__wrapper {
  width: 100%;
  background-size: cover;
  background-position: center;
}

.wp-block-eara-hero-home .eara-hero-home__ratio {
  aspect-ratio: 16 / 9;
  width: 100%;
  display: grid;
  place-items: center;
  position: relative;
}

.wp-block-eara-hero-home .eara-hero-home__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 1;
}

.wp-block-eara-hero-home .eara-hero-home__container {
  width: 100%;
  max-width: var(--container-max-width, 1200px);
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
  position: relative;
  z-index: 2;
}
